export default function file(state = [], action) {
  console.log('file state', state)
  console.log('file action', action)
  switch (action.type) {
    case 'GOT_ALL_FILES':
      console.log('reducer GOT_ALL_FILES')
      return action.payload
    case 'UPLOADED_GFILE':
      console.log('reducer UPLOADED_GFILE')
      return action.payload
    case 'DELETED_FILE':
      console.log('reducer DELETED_FILE')
      return state
    default:
      console.log('reducer Default')
      return state;
  }
};