import React, { useState, useEffect, useRef } from "react";
import { useDispatch, useSelector } from "react-redux";
import "./People.css";
import AddingForm from "./AddingForm";
import ObjectCard from "./ObjectCard";
import HeroSection from "../HeroSection";

import {
  getMembersInfo,
  postMember,
  putMember,
  deleteMember,
} from "../../redux/action/action.member";

function People({ user }) {
  const dispatch = useDispatch();
  const members = useSelector((state) => state.members);
  const [currentId, setCurrentId] = useState(null);
  const [change, setChange] = useState(false);
  const ref = useRef();
  useEffect(() => {
    refreshEmployeeList();
    setChange(false);
  }, [change]);

  function findbyId() {
    for (var i = 0; i < members.length; i++) {
      if (members[i]._id === currentId) {
        return members[i];
      }
    }
  }

  function refreshEmployeeList() {
    dispatch(getMembersInfo());
  }

  return (
    <>
      <HeroSection resultref={ref} user={user} />
      <div className="row">
        <div className="col-md-12" ref={ref}>
          <div className="jumbotron jumbotron-fluid py-4">
            <div className="container text-center">
              <h1 className="display-4">Member</h1>
            </div>
          </div>
        </div>
        <div class="container">
          <div class="row">
            <div class="col-md-3">
              {user.role === "admin" && (
                <AddingForm
                  mode="members"
                  object={findbyId()}
                  createObject={postMember}
                  editObject={putMember}
                  setCurrentId={setCurrentId}
                  setChange={setChange}
                />
              )}
            </div>
            {members.map((member) => (
              <div class="col-md-3">
                <ObjectCard
                  mode="members"
                  object={member}
                  setCurrentId={setCurrentId}
                  deleteObject={deleteMember}
                  setChange={setChange}
                />
              </div>
            ))}
          </div>
        </div>
      </div>
      <br />
    </>
  );
}
export default People;
