import * as fileApi from '../api/api.file'

export const getAllFiles = () => async (dispatch) => {
  try {
    const receiveData = await fileApi.getAllFiles();

    dispatch({ type: 'GOT_ALL_FILES', payload: receiveData.data })
  }
  catch (error) {
    console.log(error)
  }
}

export const uploadGfile = (file) => async (dispatch) => {
  try {
    const receiveData = await fileApi.postGfile(file);

    dispatch({ type: 'UPLOADED_GFILE', payload: receiveData.data })
  }
  catch (error) {
    console.log(error)
  }
}

export const deleteFile = (id) => async (dispatch) => {
  try {
    const receiveData = await fileApi.deleteFile(id)

    dispatch({ type: 'DELETED_FILE', payload: receiveData.data })
  }
  catch (error) {
    console.log(error)
  }
}