import * as userApi from '../api/api.user'

export function postLogin(user) {                                     //step1: This fucntion is called via dispatch(postLogin(user Data))
  return dispatch => {
    return userApi.postLogin(user)                                    //step2: [POST] user data to server and receiveData: respond from server
      .then(receiveData => {
        dispatch({ type: 'AUTHENED', payload: receiveData.data })    //step 3:update State of User and store action in new state(my code) in payload use reducer.user
      })
      .catch(function (error) {
      //  dispatch({ type: 'USER_ERROR', payload: error.response.data });
      });
  };
}


export function postSignup(user) {
  return dispatch => {
    return userApi.postSignup(user)
      .then(receiveData => {
        dispatch({ type: 'CREATED', payload: receiveData.data })
      })
      .catch(function (error) {
      //dispatch({ type: 'USER_ERROR', payload: error.response.data });
      });
  };
}

