export default function members(state = [], action) {
  console.log('member state', state)
  console.log('member action', action)
  switch (action.type) {
    case 'GOT_MEMBER_INFO':
      console.log('reducer member GOT_MEMBER_INFO')
      return action.payload
    case 'POSTED_MEMBER':
      console.log('reducer member POSTED_MEMBER')
      return state
    case 'PUT_MEMBER':
      console.log('reducer member PUT_MEMBER')
      return state
    case 'DELETED_MEMBER':
      console.log('reducer member DELETED_MEMBER')
      return state
    default:
      console.log('reducer member Default')
      return state;
  }
};