import { useDispatch } from 'react-redux';

const ObjectCard = ({ mode, object, setCurrentId, deleteObject, setChange }) => {
    const dispatch = useDispatch()
    
    const onDelete = (e, id) => {
        dispatch(deleteObject(id))
        setChange(true)
    }
    return (
        <div className="card" onClick={() => { setCurrentId(object._id) }}>
            <img src={'http://103.170.123.77:5000/' + mode + '/' + object.imageName} className="card-img-top rounded-circle" alt="cannot display"/>
            <div className="card-body">
                <h5>{object.employeeName}</h5>
                <span>{object.occupation}</span> <br />
                <button className="btn btn-light delete-button" onClick={(e) => onDelete(e, object._id)}>
                    <i className="far fa-trash-alt"></i>
                </button>
            </div>
        </div>
    )
}

export default ObjectCard