import React, { Component } from "react";
import {  Modal, } from "react-bootstrap";
import { connect } from 'react-redux'
import { DropdownButton, Dropdown, ButtonGroup } from 'react-bootstrap';
import { authorization } from '../../redux/action/action.management';

class UserInfo extends Component {
  constructor(props) {
    super(props);
    this.state = {
      showModal: false,
      smShow: false,
      currentId: this.props.currentId,
      givenname: "",
      familyname: "",
      username: "",
      phone: "",
      address: ""
    };
  }

  componentDidUpdate(prevProps) {
    if (this.props.currentId !== prevProps.currentId) {
      console.log('user', this.props.users)
      for (var i = 0; i < this.props.users.length; i++) {
        if (this.props.users[i]._id === this.props.currentId) {
          this.setState({
            givenname: this.props.users[i].givenname,
            familyname: this.props.users[i].familyname,
            username: this.props.users[i].username,
            role: this.props.users[i].role,
            phone: this.props.users[i].phone,
            address: this.props.users[i].address
          })
        }
      }
    }
  }

  renderProfile = () => {
    return (
      <div class="card user-card-full">
        <div class="row m-l-0 m-r-0">
          <div class="col-sm-4 bg-c-lite-green user-profile">
            <div class="card-block text-center text-white">
              <img src='./images/image_placeholder.png' className="card-img-top rounded-circle" alt="cannot display" />
              <h6 class="f-w-600">{this.state.givenname} {this.state.familyname}</h6>
            </div>
          </div>
          <div class="col-sm-8">
            <div class="card-block">
              <h3 class="m-b-20 p-b-5 b-b-default f-w-600">Information</h3>
              <div class="row">
                <div class="col-sm-6">
                  <p class="m-b-10 f-w-600">Name</p>
                  <h6 class="text-muted f-w-400">{this.state.givenname} {this.state.familyname}</h6>
                </div>
                <div class="col-sm-6">
                  <p class="m-b-10 f-w-600">Role</p>
                  {[DropdownButton].map((DropdownType, idx) => (
                    <DropdownType
                      as={ButtonGroup}
                      key={idx}
                      id={`dropdown-button-drop-${idx}`}
                      size="sm"
                      title={this.state.role === 'admin' ? "Admin" :
                          this.state.role === 'member' ? 'Member' :
                          'Standard'
                  }
                    >
                      <Dropdown.Item onClick={() => this.props.authorize({_id: this.props.currentId, role: "member"})}>Member</Dropdown.Item>
                      <Dropdown.Item onClick={() => this.props.authorize({_id: this.props.currentId, role: "standard"})}>Standard</Dropdown.Item>
                    </DropdownType>))}
                </div>
                <div class="col-sm-6">
                  <p class="m-b-10 f-w-600">Email</p>
                  <h6 class="text-muted f-w-400">{this.state.username}</h6>
                </div>
                <div class="col-sm-6">
                  <p class="m-b-10 f-w-600">Phone</p>
                  <h6 class="text-muted f-w-400">{this.state.phone}</h6>
                </div>
                <div class="col-sm-6">
                  <p class="m-b-10 f-w-600">Adress</p>
                  <h6 class="text-muted f-w-400">{this.state.address}</h6>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  };

  render() {
    return (
      <div>
        <Modal
          show={this.props.showModal}
          onHide={this.props.onClose}
          onSubmit={this.onSubmit}
          size="lg"
        >
          <Modal.Body>
            {this.renderProfile()}
          </Modal.Body>
        </Modal>
      </div>
    );
  }
}

const mapStateToProps = state => ({
  users: state.management
});

const mapDispatchToProps = (dispatch) => {
  return {
    authorize: (role) => dispatch(authorization(role))
  }
};
export default connect(mapStateToProps, mapDispatchToProps)(UserInfo)