const UserTuple = ({ user, setCurrentId, setShowModal }) => {

    return (
            <tr class="table-light" onClick={() => { 
                setCurrentId(user._id)
                setShowModal(true) }}>
                <td>{user.givenname}</td>
                <td>{user.username}</td>
                <td>{user.googleaccount === 'Google Account'? 'Google Account': user.googleaccount === true? 'True' : 'False'}</td>
                <td>{user.role === 'admin' ? "Admin" : user.role === 'member' ? 'Member' : 'Standard'}</td>
            </tr>
    )
}

export default UserTuple