import axios from 'axios';

const equipUrl = 'http://103.70.123.77/equipmentcontroller';
const config = { withCredentials: true }
export const getEquipsInfo = () => axios.get(equipUrl, config)
export const postEquip = (equip) => axios.post(equipUrl, equip, config)
export const putEquip = (equip) => axios.put(equipUrl, equip, config)
export const deleteEquip = (id) => axios.delete(equipUrl + '/' + id.toString())