import * as equipApi from '../api/api.equipment'

export const getEquipsInfo = () => async (dispatch) => {
  try {
    const receiveData = await equipApi.getEquipsInfo();
    console.log('getEquipsInfo', receiveData.data)
    dispatch({ type: 'GOT_EQUIP_INFO', payload: receiveData.data })
  }
  catch (error) {
    console.log(error)
  }
}

export const postEquip = (equip) => async (dispatch) => {
  try {
    const receiveData = await equipApi.postEquip(equip);

    dispatch({ type: 'POSTED_EQUIP', payload: receiveData.data })
  }
  catch (error) {
    console.log(error)
  }
}

export const putEquip = (equip) => async (dispatch) => {
  try {
    const receiveData = await equipApi.putEquip(equip);

    dispatch({ type: 'PUT_EQUIP', payload: receiveData.data })
  }
  catch (error) {
    console.log(error)
  }
}

export const deleteEquip = (id) => async (dispatch) => {
  try {
    const receiveData = await equipApi.deleteEquip(id);

    dispatch({ type: 'DELETED_EQUIP', payload: receiveData.data })
  }
  catch (error) {
    console.log(error)
  }
}