import axios from 'axios';

const researchUrl = 'http://103.70.123.77/researchcontroller';
const config = { withCredentials: true }
export const getResearchsInfo = () => axios.get(researchUrl, config)
export const createResearch = (research) => axios.post(researchUrl, research, config)
export const putResearch = (research) => axios.put(researchUrl, research, config)