import React, {useState, useEffect} from 'react';
import { Button } from './Button';
import './HeroSection.css';
import LoginForm from './pages/LoginForm';

const HeroSection = ({resultref, user}) => {
    const scrollToRef = (e) => {
        e.preventDefault();
        resultref.current.scrollIntoView({
            behavior: 'smooth',
            block: 'start',
            inline: 'start',
        });
    };
    const [buttonFunc, setButtonFunc] = useState(user.role === 'admin' || user.role === 'member' ? 'Profile' : 'LOG IN');
    const [showModal, setShowModal] = useState(false);
    const [button, setButton] = useState(true);
    return(
        <div className = 'hero-container'>
            <video src = "./videos/thinkalpha.mp4" autoPlay loop preload muted/>
            <h1>Think Alpha Company</h1>
            <p>Khng bao gi l tht bi, tt c ch l th thch</p>
            <div className = 'hero-btns'>
                <Button 
                className ='btns'
                buttonStyle = 'btn--outline'
                buttonSize = 'btn--large'
                onClick = {() => setShowModal(true)}>
                    {buttonFunc}
                </Button>
                {!showModal && button &&<Button                
                className ='btns'
                buttonStyle = 'btn--primary'
                buttonSize = 'btn--large'
                onClick = {scrollToRef}>
                    EXPLORE <i className = 'far fa-play-circle'/>
                </Button>}
            </div>
            <LoginForm user={user} showModal={showModal} onClose={() => setShowModal(false)} />
        </div>
    )
}
export default HeroSection;