import React, { useState, useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import UserTuple from './UserTuple';
import UserInfo from './UserInfo';
import { getAllUsers } from '../../redux/action/action.management';

function Management() {
    const dispatch = useDispatch();
    const users = useSelector(state => state.management)
    const [currentId, setCurrentId] = useState(null)
    const [showModal, setShowModal] = useState(false)

    useEffect(() => {
        dispatch(getAllUsers())
    }, [dispatch])

    return (
        <>
            <div className="row">
                <div className="col-md-12">
                    <div className="jumbotron jumbotron-fluid py-4">
                        <div className="container text-center">
                            <h1 className="display-4">User Management</h1>
                        </div>
                    </div>
                </div>
                <div class="container">
                    <table className="table">
                    <tbody>
                        <tr>
                            <td>Name</td>
                            <td>Email</td>
                            <td>Google Account</td>
                            <td>Role</td>
                        </tr>
                        {users.map((user) => (
                            <UserTuple user={user} setCurrentId={setCurrentId} setShowModal={setShowModal} />
                        ))}
                        </tbody>
                    </table>
                </div>
                <UserInfo currentId={currentId} showModal={showModal} onClose={() => setShowModal(false)} />
            </div>
        </>
    )
}
export default Management;