export default function user(state = [], action) {
  console.log('user state', state)
  console.log('user action', action)
  switch (action.type) {
    case 'GOT_USER_INFO':
      console.log('reducer GOT_USER_INFO')
      return action.payload
    case 'LOGGED_OUT':
      console.log('reducer LOGGED_OUT')
      return action.payload
    case 'CREATED':
      console.log('reducer CREATED')
      return action.payload
    case 'AUTHENED':
      console.log('reducer AUTHENED')
      return action.payload
    case 'CHANGED_PASSWORD':
      console.log('reducer CHANGED_PASSWORD')
      return action.payload
    case 'PUT_USER':
      console.log('reducer member PUT_USER')
      return state
    case 'FETCH_GTOKEN':
      console.log('reducer member FETCH_GTOKEN')
      return state
    case 'GOT_PASSWORD':
      console.log('reducer member GOT_PASSWORD')
      return state
    default:
      console.log('reducer Default')
      return state;
  }
};