import React, { useEffect, useRef } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { getUserInfo } from './redux/action/action.user';
import './App.css';
import Navbar from './components/Navbar';
import { BrowserRouter as Router, Switch, Route } from 'react-router-dom';
import Home from './components/pages/Home';
import Research from './components/pages/Research';
import Equipments from './components/pages/Equipment';
import Files from './components/pages/Files';
import Gallery from './components/pages/Gallery';
import Management from './components/pages/Management'
import Cooperation from './components/pages/Cooperation';
import People from './components/pages/People';
import Contact from './components/pages/Contact';
import Publication from './components/pages/Publication';
import Footer from './components/Footer';
import Add from './components/pages/Add';
import Edit from './components/pages/Edit';


function App() {
  const dispatch = useDispatch();
  const user = useSelector(state => state.users);
  useEffect(() => {
    dispatch(getUserInfo());
  }, [dispatch])
  return (
    <>
      <Router>
        <Navbar user = {user}/>
        <Switch>
          <Route path='/' exact component={Home} />
          <Route path='/Research' component={Research} />
          <Route path='/Equipment' render={() => (<Equipments user = {user}/>)} />
          <Route path='/File' render={() => (<Files user = {user}/>)} />
          <Route path='/Edit/:_id' component={Edit}/>
          <Route path='/Add' component={Add}/>
          <Route path='/Gallery'  component={Gallery} />
          <Route path='/Management' render={() => (<Management user = {user}/>)} />
          <Route path='/Cooperation' component={Cooperation} />
          <Route path='/People' render={() => (<People user = {user}/>)} />
          <Route path='/Contact' component={Contact} />
          <Route path='/Public' component={Publication} />
          <Route path='/LinkedIn' component={() => {
            window.location.href = 'https://www.linkedin.com/';
            return null;
          }} />
          <Route path='/Facebook' component={() => {
            window.location.href = 'https://www.facebook.com/';
            return null;
          }} />
          <Route path='/Youtube' component={() => {
            window.location.href = 'https://www.youtube.com/';
            return null;
          }} />
          <Route path='/Twitter' component={() => {
            window.location.href = 'https://twitter.com/';
            return null;
          }} />
        </Switch>
        <Footer />
      </Router>
    </>
  );
}

export default App;
