import React, { useState, useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import FileTuple from './FileTuple';
import { getAllFiles, uploadGfile } from '../../redux/action/action.file';
import './Files.css'
function Files({ }) {
    const dispatch = useDispatch();
    const files = useSelector(state => state.files)
    const [values, setValues] = useState(null)
    const [change, setChange] = useState(false)

    useEffect(() => {
        dispatch(getAllFiles())
        setChange(false)
    }, [change])

    const showPreview = e => {
        if (e.target.files && e.target.files[0]) {
            let imageFile = e.target.files[0];
            const reader = new FileReader();
            reader.onload = x => {
                setValues({
                    ...values,
                    imageFile,
                    imageSrc: x.target.result
                })
            }
            reader.readAsDataURL(imageFile)
        }
    }

    const handleFormSubmit = e => {
        const data = new FormData()
        data.append("file", values.imageFile);
        dispatch(uploadGfile(data))
    }

    return (
        <>
            <div class="container">

                <div class="mt-4">
                    <table class="table">
                        <thead>
                            <tr >
                                <th scope="col">Name</th>
                                <th scope="col">Decription</th>
                            </tr>
                        </thead>
                        <tbody>
                            {files.map((file) => (
                                <FileTuple file={file} setChange={setChange} />
                            ))}
                        </tbody>
                    </table>
                </div>
                <form autoComplete="off" noValidate onSubmit={handleFormSubmit}>
                    <div className="card-body">
                        <div className="form-group">
                            <input type="file"
                                onChange={showPreview} id="image-uploader" />
                        </div>
                        <div className="form-group text-center">
                            <button type="submit" className="btn btn-light" onClick={() => setChange(true)}>Submit</button>
                        </div>
                    </div>
                </form>
            </div>
        </>
    )
}
export default Files;