import axios from 'axios';

const userUrl = '/usercontroller';
const config = { withCredentials: true }
export const getUserInfo = () => axios.get(userUrl, config)
export const getLogout = () => axios.get(userUrl + '/logout', config)
export const postLogin = (user) => axios.post(userUrl + '/login', user, config);
export const postSignup = (user) => axios.post(userUrl + '/signup', user, config);
export const patchUser = (user) => axios.patch(userUrl, user, config)
export const postGToken = (token) => axios.post(userUrl + '/gtoken', token, config)
export const changePassword = (user) => axios.patch(userUrl + '/change', user, config)
export const forgotPassword = (user) => axios.patch(userUrl + '/forgot', user, config)


