import React, { useState } from "react";
import ReactQuill from "react-quill";
import EditorToolbar, { modules, formats } from "./EditorToolbar";
import "react-quill/dist/quill.snow.css";
import "./TextEditor.css";
import { useHistory } from "react-router-dom";
import { useDispatch } from 'react-redux';
import { createResearch } from '../../redux/action/action.research'

function Add() {
  
  const history = useHistory()
  const dispatch = useDispatch()
  const [userInfo, setuserInfo] = useState({
    description: ''
  });

  const ondescription = (value) => {
    setuserInfo({
      ...userInfo,
      description: value
    });
  }

  const [isError, setError] = useState(null);

  const addDetails = async (event) => {
    try {
      event.preventDefault();
      event.persist();
      if (userInfo.description.length < 50) {
        setError('Required, Add description minimum length 50 characters');
        return;
      } else {
        dispatch(createResearch(userInfo))
        history.push({
          pathname: '/Research',
      });

      }
    }
      catch (error) { throw error; }
  }

  return (
    <>

      <div className="App">
        <div className="container">
          <div className="row">
            <form onSubmit={addDetails} className="update__forms">
              <h3 className="myaccount-content"> Add  </h3>
              <div className="form-row">
                <div className="clearfix"></div>
                <div className="form-group col-md-12 editor">
                  <label className="font-weight-bold"> Description <span className="required"> * </span> </label>
                  <EditorToolbar toolbarId={'t1'} />
                  <ReactQuill
                    theme="snow"
                    value={userInfo.description}
                    onChange={ondescription}
                    placeholder={"Write something awesome..."}
                    modules={modules('t1')}
                    formats={formats}
                  />
                </div>
                <br />
                <br />
                {isError !== null && <div className="errors"> {isError} </div>}
                <div className="form-group col-sm-12 text-right">
                  <button type="submit" className="btn btn__theme"> Submit  </button>
                </div>
              </div>
            </form>
          </div>
        </div>
      </div>
    </>
  )
}
export default Add