export default function equips(state = [], action) {
  console.log('equip state', state)
  console.log('equip action', action)
  switch (action.type) {
    case 'GOT_EQUIP_INFO':
      console.log('reducer equip GOT_EQUIP_INFO')
      return action.payload
    case 'POSTED_EQUIP':
      console.log('reducer equip POSTED_EQUIP')
      return state
    case 'PUT_EQUIP':
      console.log('reducer equip PUT_EQUIP')
      return state
    case 'DELETED_EQUIP':
      console.log('reducer equip DELETED_EQUIP')
      return state
    default:
      console.log('reducer equip Default')
      return state;
  }
};