import React from "react";
import "./Footer.css";
import { Link } from "react-router-dom";

function Footer() {
  return (
    <div className="footer-container">
      <section className="footer-subscription">
        <p className="footer-subscription-heading">Ni g  (heading)</p>
        <p className="footer-subscription-text">
          The Think Alpha Company acknowledges the urgency of science and
          technology for current and future problems while paying respect to the
          values of the past. We also believe that humans with wisdom will
          continue to be the center of humankind's development wheel.
        </p>
      </section>
      <div className="footer-links">
        <div className="footer-link-wrapper">
          <div className="footer-link-items">
            <h2 className="footer-heading">About Us</h2>
            <Link to="/">How we works</Link>
            <Link to="/People">Careers</Link>
            <Link to="/Investor">Investors</Link>
            <Link to="/TermsOfService">Terms of Service</Link>
          </div>
          <div className="footer-link-items">
            <h2 className="footer-heading">Contact Us</h2>
            <Link className="footer-link-sub" to="/Contact">
              Contact
            </Link>
            <Link className="footer-link-sub" to="/Support">
              Support
            </Link>
            <Link className="footer-link-sub" to="/Cooperation">
              Cooperation
            </Link>
            <Link className="footer-link-sub" to="/Sponsorships">
              Sponsorships
            </Link>
          </div>
        </div>
        <div className="footer-link-wrapper">
          <div className="footer-link-items">
            <h2 className="footer-heading">Videos</h2>
            <Link className="footer-link-sub" to="/Video">
              Video
            </Link>
            <Link className="footer-link-sub" to="/Gallery">
              Gallery
            </Link>
            <Link className="footer-link-sub" to="/Agency">
              Agency
            </Link>
            <Link className="footer-link-sub" to="/Influencer">
              Influencer
            </Link>
          </div>
          <div className="footer-link-items">
            <h2 className="footer-heading">Social Media</h2>
            <a
              className="footer-link-sub"
              href="https://www.facebook.com/Qind.Shin/"
            >
              Facebook{" "}
            </a>
            <a className="footer-link-sub" href="https://www.youtube.com/">
              Youtube{" "}
            </a>
            <a className="footer-link-sub" href="https://twitter.com/">
              Twitter{" "}
            </a>
            <a className="footer-link-sub" href="https://www.linkedin.com/">
              LinkedIn{" "}
            </a>
          </div>
        </div>
      </div>
      <section className="social-media">
        <div className="social-media-wrap">
          <div className="footer-logo">
            <Link className="footer-link-sub" to="/" className="social-logo">
              Think Alpha
              <i className="fab fa-galactic-republic" />
            </Link>
          </div>
          <small className="website-rights">Think Alpha  2021</small>
          <div className="social-icons">
            <Link
              className="social-icon-link facebook"
              to="/Facebook"
              target="_blank"
              aria-label="Facebook"
            >
              <i className="fab fa-facebook-f" />
            </Link>
            <Link
              className="social-icon-link youtube"
              to="/Youtube"
              target="_blank"
              aria-label="Youtube"
            >
              <i className="fab fa-youtube" />
            </Link>
            <Link
              className="social-icon-link twitter"
              to="/Twitter"
              target="_blank"
              aria-label="Twitter"
            >
              <i className="fab fa-twitter" />
            </Link>
            <Link
              className="social-icon-link linkedin"
              target="_blank"
              aria-label="LinkedIn"
              to="/LinkedIn"
            >
              <i className="fab fa-linkedin" />
            </Link>
          </div>
        </div>
      </section>
    </div>
  );
}

export default Footer;
