import * as researchApi from '../api/api.research'

export const getResearchsInfo = () => async (dispatch) => {
  try {
    const receiveData = await researchApi.getResearchsInfo();
    console.log('getResearchsInfo', receiveData.data)
    dispatch({ type: 'GOT_RESEARCHS_INFO', payload: receiveData.data })
  }
  catch (error) {
    console.log(error)
  }
}

export const createResearch = (research) => async (dispatch) => {
  try {
    const receiveData = await researchApi.createResearch(research);
    console.log('createResearch', receiveData.data)
    dispatch({ type: 'CREATE_RESEARCH', payload: receiveData.data })
  }
  catch (error) {
    console.log(error)
  }
}

export const putResearch = (research) => async (dispatch) => {
  try {
    const receiveData = await researchApi.putResearch(research);
    console.log('getResearchInfo', receiveData.data)
    dispatch({ type: 'PUT_`RESEARCH`', payload: receiveData.data })
  }
  catch (error) {
    console.log(error)
  }
}