import axios from 'axios';
const memberUrl = '/membercontroller';
const config = { withCredentials: true }
export const getMembersInfo = () => axios.get(memberUrl, config)
export const postMember = (member) => axios.post(memberUrl, member, config)
export const putMember = (member) => axios.put(memberUrl, member, config)
export const deleteMember = (id) => axios.delete(memberUrl + '/' + id.toString())