import React, { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';
import { Button } from './Button';
import './Navbar.css';
import LoginForm from './pages/LoginForm';

function Navbar({ user }) {
    const [showModal, setShowModal] = useState(false)
    console.log('navuser', user)
    const [buttonFunc, setButtonFunc] = useState(user.role === 'admin' || user.role === 'member' ? 'Profile' : 'LOG IN')
    const [click, setClick] = useState(false);
    const [button, setButton] = useState(true);
    const handleClick = () => setClick(!click);
    const closeMobileMenu = () => setClick(false);
    const showButton = () => {
        if (window.innerWidth <= 1450) {
            setButton(false);
        } else {
            setButton(true);
        }
    };
    useEffect(() => {
        showButton();
        user.role === 'admin' || user.role === 'member' ? setButtonFunc('Profile') : setButtonFunc('LOGIN')
    }, [user]);
    window.addEventListener('resize', showButton);
    return (
        <>
            <nav className="navbar">
                <div className="navbar-container">
                    <Link to="/" className="navbar-logo" onClick={closeMobileMenu}>
                        <pre class="tab">Think Alpha <i class="fab fa-galactic-republic"></i></pre>
                    </Link>
                    <div className='menu-icon' onClick={handleClick}>
                        <i className={click ? 'fas fa-times' : 'fas fa-bars'} />
                    </div>
                    <ul className={click ? 'nav-menu active' : 'nav-menu'}>
                        <li className='nav-item'>
                            <Link to='/' className='nav-links' onClick={closeMobileMenu}>
                                About us
                            </Link>
                        </li>
                        <li className='nav-item'>
                            <Link to='/Research' className='nav-links' onClick={closeMobileMenu}>
                                Research
                            </Link>
                        </li>                 
                        <li className='nav-item'>
                            <Link to='/Equipment' className='nav-links' onClick={closeMobileMenu}>
                                Equipment
                            </Link>
                        </li>
                        <li className='nav-item'>
                            <Link to='/Gallery' className='nav-links' onClick={closeMobileMenu}>
                                Gallery
                            </Link>
                        </li>
                        <li className='nav-item'>
                            <Link to='/People' className='nav-links' onClick={closeMobileMenu}>
                                People
                            </Link>
                        </li>
                        <li className='nav-item'>
                            <Link to='/Public' className='nav-links' onClick={closeMobileMenu}>
                                Publications
                            </Link>
                        </li>
                        <li className='nav-item'>
                            <Link to='/Cooperation' className='nav-links' onClick={closeMobileMenu}>
                                Cooperation
                            </Link>
                        </li>
                        <li className='nav-item'>
                            <Link to='/Contact' className='nav-links' onClick={closeMobileMenu}>
                                Contact
                            </Link>
                        </li>
                        <li className='nav-item-signin'>
                            <Link to='/' 
                            className='nav-links-mobile' 
                            onClick={() => {closeMobileMenu();setShowModal(true);}}>
                                LOG IN
                            </Link>
                        </li>
                    </ul>
                    {!showModal && button && <Button className='btn--pos' onClick={() => setShowModal(true)} buttonStyle='btn--outline'>{buttonFunc}</Button>}
                </div>
                <LoginForm user={user} showModal={showModal} onClose={() => setShowModal(false)} />
            </nav>
        </>
    );
}

export default Navbar;
