import React, { useState, useEffect } from 'react';
import { useDispatch } from 'react-redux';

import './People.css';

const defaultImageSrc = './images/image_placeholder.png';
const initialFieldValues = {
    _id: 0,
    employeeName: '',
    occupation: '',
    imageName: '',
    imageSrc: defaultImageSrc,
    imageFile: null
}

const AddingForm = ({ mode, object, createObject, editObject, setCurrentId, setChange }) => {
    const dispatch = useDispatch()
    const [values, setValues] = useState(initialFieldValues)
    const [errors, setErrors] = useState({})

    useEffect(() => {
        if (object != null){
            setValues({employeeName: object.employeeName, 
                occupation: object.occupation,
                imageName: object.imageName,
                imageSrc: 'http://103.70.123.77/' + mode + '/' + object.imageName})
        }
    }, [object])

    const showPreview = e => {

        if (e.target.files && e.target.files[0]) {
            let imageFile = e.target.files[0];
            const reader = new FileReader();
            reader.onload = x => {
                setValues({
                    ...values,
                    imageFile: imageFile,
                    imageSrc: x.target.result
                })
            }
            reader.readAsDataURL(imageFile)
        }
    }

    const validate = () => {
        let temp = {}
        temp.employeeName = values.employeeName === "" ? false : true;
        temp.imageSrc = values.imageSrc === defaultImageSrc ? false : true;
        setErrors(temp)
        return Object.values(temp).every(x => x === true)
    }

    const resetForm = () => {
        setCurrentId(0)
        setValues(initialFieldValues)
        setErrors({})
    }

    const handleFormSubmit = e => {
        e.preventDefault()
        if (validate()) {
            const formData = {
                employeeName: values.employeeName,
                occupation: values.occupation,
                imageName: values.imageName,
                imageFile: values.imageFile
            }
            const data = new FormData()
            data.append("employeeName", formData.employeeName);
            data.append("occupation", formData.occupation);
            data.append("imageName", formData.imageName);
            data.append("imageFile", formData.imageFile);
            if (object === undefined) {
                dispatch(createObject(data))
            }
            else {
                data.append("_id", object._id)
                console.log('addingform', values)
                dispatch(editObject(data))
            }
            setChange(true)
        }
    }

    const applyErrorClass = field => ((field in errors && errors[field] === false) ? ' invalid-field' : '')

    return (
        <>
            <form autoComplete="off" noValidate onSubmit={handleFormSubmit}>
                <div className="card">
                    <img src={values.imageSrc} className="card-img-top" alt='cannot display' />
                    <div className="card-body">
                        <div className="form-group">
                            <input type="file" accept="image/*" className={"form-control-file" + applyErrorClass('imageSrc')}
                                onChange={showPreview} id="image-uploader" />
                        </div>
                        <div className="form-group">
                            <input className={"form-control" + applyErrorClass('employeeName')} placeholder="Employee Name" name="employeeName"
                                value={values.employeeName}
                                onChange={(e) => setValues({ ...values, employeeName: e.target.value })} />
                        </div>
                        <div className="form-group">
                            <input className={"form-control" + applyErrorClass('occupation')} placeholder="Occupation" name="occupation"
                                value={values.occupation}
                                onChange={(e) => setValues({ ...values, occupation: e.target.value })} />
                        </div>
                        <div className="form-group text-center">
                            <button type="submit" className="btn btn-light">Submit</button>
                            <button type="submit" className="btn btn-light" onClick={() => resetForm()}>Cancel</button>
                        </div>
                    </div>
                </div>
            </form>
        </>
    )
}

export default AddingForm