import { Link } from 'react-router-dom'
import { useDispatch } from 'react-redux';
import { deleteFile } from '../../redux/action/action.file'

const FileTuple = ({ file, setChange }) => {
    const dispatch = useDispatch()
    return (
        <tr>
            <td>{file.name}</td>
            <td>{file.mimeType}</td>
            <Link to={{ pathname: `https://drive.google.com/file/d/${file.id}/view?usp=drivesdk` }} target="_blank" > View </Link>
            <Link to={{ pathname: `https://drive.google.com/uc?id=${file.id}&export=download` }} target="_blank" > Download </Link>
            <button onClick={() => {
                dispatch(deleteFile(file.id))
                setChange(true)
                }} className="btn btn-light">Delete</button>
        </tr>
    )
}

export default FileTuple