import * as managementApi from '../api/api.management'

export const getAllUsers = () => async (dispatch) => {
  try {
    const receiveData = await managementApi.getAllUsers();

    dispatch({ type: 'GOT_ALL_USERS', payload: receiveData.data })
  }
  catch (error) {
    console.log(error)
  }
}

export const authorization = (role) => async (dispatch) => {
  try {
    const receiveData = await managementApi.authorization(role);

    dispatch({ type: 'AUTHORIZATION', payload: receiveData.data })
  }
  catch (error) {
    console.log(error)
  }
}