export default function management(state = [], action) {
  console.log('management state', state)
  console.log('managements action', action)
  switch (action.type) {
    case 'GOT_ALL_USERS':
      console.log('reducer GOT_ALL_USERS')
      return action.payload
    case 'AUTHORIZATION':
      console.log('reducer AUTHORIZATION')
      return action.payload
    default:
      console.log('reducer Default')
      return state;
  }
};