import React, { useEffect, useRef } from "react";
import "../../App.css";
import "./Contact.css";
import Map from "../Map";
import HeroSection from "../HeroSection";
import { useDispatch, useSelector } from "react-redux";


function Contact() {
    const ref = useRef();
    const user = useSelector((state) => state.users);
  return (
    <>
      <HeroSection resultref={ref} user={user} />
      <div className="row">
        <div className="col-md-12" ref={ref}>
          <div className="jumbotron jumbotron-fluid py-4">
            <div className="container text-center">
              <h1 className="display-4">Contact us</h1>
            </div>
          </div>
        </div>
      </div>
      <div className="contact">
        <div className="email">
          <h4> Email: admin@thinkalpha.com.vn </h4>
        </div>
        <div className="tel">
          <h4>Tel: 036 342 342 353</h4>
        </div>

        <h1>Address</h1>

        <div className="address">
          <h4>
            {" "}
            662/6A Su Van Hanh Street, Ward 12, District 10, Ho Chi Minh City{" "}
          </h4>
        </div>

        <Map
          // googleMapURL={`https://maps.googleapis.com/maps/api/js?v=3.exp&libraries=geometry,drawing,places&key=${
          //     process.env.REACT_APP_GOOGLE_KEY
          //     }`}
          googleMapURL={`https://maps.googleapis.com/maps/api/js?key=${"AIzaSyDiKF3XgbNp4bhBBDSEVoYvnYNKcC_dSdw"}&callback=initMap`}
          loadingElement={<div style={{ height: `100%` }} />}
          containerElement={
            <div
              style={{
                height: `70vh`,
                margin: `auto`,
                border: "0px solid white",
              }}
            />
          }
          mapElement={<div style={{ height: `100%` }} />}
        />
      </div>
    </>
  );
}

export default Contact;
