import * as memberApi from '../api/api.member'

export const getMembersInfo = () => async (dispatch) => {
  try {
    const receiveData = await memberApi.getMembersInfo();
    console.log('getMembersInfo', receiveData.data)
    dispatch({ type: 'GOT_MEMBER_INFO', payload: receiveData.data })
  }
  catch (error) {
    console.log(error)
  }
}

export const postMember = (member) => async (dispatch) => {
  try {
    console.log('day la member', member.imageFile)
    const receiveData = await memberApi.postMember(member);

    dispatch({ type: 'POSTED_MEMBER', payload: receiveData.data })
  }
  catch (error) {
    console.log(error)
  }
}

export const putMember = (member) => async (dispatch) => {
  try {
    const receiveData = await memberApi.putMember(member);

    dispatch({ type: 'PUT_MEMBER', payload: receiveData.data })
  }
  catch (error) {
    console.log(error)
  }
}

export const deleteMember = (id) => async (dispatch) => {
  try {
    const receiveData = await memberApi.deleteMember(id);

    dispatch({ type: 'DELETED_MEMBER', payload: receiveData.data })
  }
  catch (error) {
    console.log(error)
  }
}