import axios from 'axios';
const fileUrl = 'http://103.70.123.77/filecontroller';
const config = { withCredentials: true }
export const getAllFiles = () => axios.get(fileUrl, config)
export const postGfile = (file) => axios.post(fileUrl, file, config)
export const deleteFile = (id) => axios.delete(fileUrl + '/' + id.toString())