import React, { useState, useEffect } from 'react';
import ReactQuill from "react-quill";
import EditorToolbar, { modules, formats } from "./EditorToolbar";
import "react-quill/dist/quill.snow.css";
import "./TextEditor.css";
import { useHistory } from "react-router-dom";
import { getResearchsInfo, putResearch } from '../../redux/action/action.research';
import { useDispatch, useSelector } from 'react-redux';

function Edit(props) {
  let history = useHistory();
  const researchs = useSelector(state => state.researchs)
  const dispatch = useDispatch()
  const [researchInfo, setResearchInfo] = useState({ _id: '', description: '' });

  useEffect(() => {
    dispatch(getResearchsInfo())
  }, [dispatch]);

  useEffect(() => {
    for (var i = 0; i < researchs.length; i++) {
      if (researchs[i]._id === props.match.params._id) {
        setResearchInfo({
          _id: props.match.params._id,
          description: researchs[i].description
        })
      }
    }
  }, [researchs]);

  const ondescription = (value) => {
    setResearchInfo({
      _id: props.match.params._id,
      description: value
    });
  }
  
  const [isError, setError] = useState(null);
  const PoemAddbooks = (event) => {
    try {
      event.preventDefault();
      event.persist();
      if (researchInfo.length < 50) {
        setError('Required, Add description minimum length 50 characters');
        return;
      } else {
        dispatch(putResearch(researchInfo))
        history.push({
          pathname: '/Research',
      });
     }
    }
    catch (error) { throw error; }
  }

  return (
    <div className="App">
      <div className="container">
        <div className="row">
          <form onSubmit={PoemAddbooks} className="update__forms">
            <h3 className="myaccount-content"> Edit   </h3>
            <div className="form-row">
              <div className="form-group col-md-12 editor">
                <label className="font-weight-bold"> Description <span className="required"> * </span> </label>
                <EditorToolbar toolbarId={'t1'} />
                <ReactQuill
                  theme="snow"
                  value={researchInfo.description}
                  onChange={ondescription}
                  modules={modules('t1')}
                  formats={formats}
                />
              </div>
              <br />
              <div className="form-group col-md-12 editor">
                <label className="font-weight-bold"> Additional Information </label>
                <EditorToolbar toolbarId={'t2'} />
              </div>
              <br />
              {isError !== null && <div className="errors"> {isError} </div>}
              <div className="form-group col-sm-12 text-right">
                <button type="submit" className="btn btn__theme"> Submit  </button>
              </div>
            </div>
          </form>
        </div>
      </div>
    </div>
  )
}
export default Edit