import { combineReducers } from 'redux';
import users from './reducer.user'
import members from './reducer.member'
import management from './reducer.management'
import equipments from './reducer.equipment'
import researchs from './reducer.research'
import files from './reducer.file'
import errors from './reducer.error'

export const reducers = combineReducers({ users, members, management, equipments, researchs, files, errors });
