import React, { useState, useEffect, useRef } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import './Equipment.css';
import HeroSection from '../HeroSection';
import AddingForm from './AddingForm';
import ObjectCard from './ObjectCard';

import { getEquipsInfo, postEquip, putEquip, deleteEquip } from '../../redux/action/action.equipment';

function Equipment({ user }) {
    const dispatch = useDispatch();
    const equipments = useSelector(state => state.equipments);
    const ref = useRef();
    //const user = useSelector(state => state.users);
    const [currentId, setCurrentId] = useState(null)
    const [change, setChange] = useState(false)

    useEffect(() => {
        refreshEmployeeList()
        setChange(false)
    }, [change])

    function findbyId() {
        for (var i = 0; i < equipments.length; i++) {
            if (equipments[i]._id === currentId) {
                return equipments[i];
            }
        }
    }

    function refreshEmployeeList() {
        dispatch(getEquipsInfo())
    }

    return (
        <>
        <HeroSection resultref={ref} user={user}/>
            <div className="row">
                <div className="col-md-12" ref ={ref}>
                    <div className="jumbotron jumbotron-fluid py-4" >
                   
                        <div className="container text-center" >
                            
                            <h1 className="display-4" >Equipments</h1>
                        </div>
                    </div>
                </div>
                <div class="container">
                    <div class="row">
                        <div class="col-md-3">
                            {user.role === 'admin' && <AddingForm
                                mode="equipments"
                                object={findbyId()}
                                createObject={postEquip}
                                editObject={putEquip}
                                setCurrentId={setCurrentId}
                                setChange={setChange}
                            />}
                        </div>
                        {equipments.map((equipment) => (
                            <div class="col-md-3">
                                <ObjectCard
                                    mode="equipments"
                                    object={equipment}
                                    setCurrentId={setCurrentId}
                                    deleteObject={deleteEquip}
                                    setChange={setChange}
                                />
                            </div>
                        ))}
                    </div>
                </div>
            </div>
            <br />
        </>
    )
}
export default Equipment;