import React, { Component } from "react";
import { Button, Modal, } from "react-bootstrap";
import { connect } from 'react-redux';
import { postLogin, postSignup, getLogout, patchUser, fetchGToken, changePassword, forgotPassword } from '../../redux/action/action.user';
import { clearError } from '../../redux/action/action.error'
import './LoginForm.css'

class LoginForm extends Component {
  constructor(props) {
    super(props);
    this.state = {
      showModal: false,
      smShow: false,
      givenname: this.props.user.givenname,
      familyname: this.props.user.familyname,
      username: this.props.user.username,
      password: "",
      newpassword: "",
      phone: this.props.user.phone,
      address: this.props.user.address,
      client_email: this.props.user.client_email,
      private_key: this.props.user.private_key,
      mode: "login"
    };
  }

  componentDidUpdate(prevProps) {
    if (this.props.user !== prevProps.user) {
      this.props.user.role === "admin" || this.props.user.role === "member" ? this.setMode("profile") : this.setMode("login");
    }
  }

  setMode = mode => {
    this.setState({
      mode
    });
    this.props.clearError()
  };

  failure = response => {
    console.log("fail", response)
  }

  close = () => {
    this.props.onClose()
    this.props.user.role === "admin" || this.props.user.role === "member" ? this.setMode("profile") : this.setMode("login");
    this.props.clearError()
  }

  error = () => {
    if (this.props.error.length !== 0)
      return (<div className="aler5t alert-danger alert-sm">
        <button type="button" className="close" data-dismiss="alert" aria-hidden="true" onClick={this.props.clearError}></button>
        <span className="fw-semi-bold">Error: </span> {this.props.error}
      </div>)
  }

  loadFile = e => {
    if (e.target.files && e.target.files[0]) {
      const reader = new FileReader();
      reader.onload = this.onReaderLoad;
      reader.readAsText(e.target.files[0]);
    }
  }

  onReaderLoad = e => {
    const jsonfile = JSON.parse(e.target.result);
    this.setState({ client_email: jsonfile.client_email, private_key: jsonfile.private_key })
  }

  renderChangePassword = () => {
    return (
      <fieldset>
        <div className="form-group has-feedback required">
          <label htmlFor="login-email" className="col-sm-5">Your Password</label>
          <div className="col-sm-7">
            <span className="form-control-feedback" aria-hidden="true"></span>
            <input
              type="password"
              name="password"
              id="password"
              className="form-control"
              placeholder="Password"
              onChange={(e) => this.setState({ password: e.target.value })}
              required
            />
          </div>
        </div>
        <div className="form-group has-feedback required">
          <label htmlFor="login-password" className="col-sm-5">Your New Password</label>
          <div className="col-sm-7">
            <span className="form-control-feedback" aria-hidden="true"></span>
            <div className="login-password-wrapper">
              <input
                type="password"
                name="newpassword"
                id="login-password"
                className="form-control"
                placeholder="New Password"
                onChange={(e) => this.setState({ newpassword: e.target.value })}
                required
              />
            </div>
          </div>
        </div>
        <button type="submit" className="btn btn-light"
          onClick={() => { this.props.changePassword({ username: this.state.username, password: this.state.password, newpassword: this.state.newpassword }) }}> Enter
          <span className="icon-arrow-right2 outlined">
          </span>
        </button>
      </fieldset>
    )
  }

  renderProfile = () => {
    return (
      <div class="card user-card-full">
        <div class="row m-l-0 m-r-0">
          <div class="col-sm-4 bg-c-lite-green user-profile">
            <div class="card-block text-center text-white">
              <img src='./images/image_placeholder.png' className="card-img-top rounded-circle" alt="cannot display" />
              <h6 class="f-w-600">{this.props.user.givenname} {this.props.user.familyname}</h6>
            </div>
          </div>
          <div class="col-sm-8">
            <div class="card-block">
              <h6 class="m-b-20 p-b-5 b-b-default f-w-600">Information</h6>
              <div class="row">
                <div class="col-sm-6">
                  <p class="m-b-10 f-w-600">First Name</p>
                  <h6 class="text-muted f-w-400">{this.props.user.givenname}</h6>
                </div>
                <div class="col-sm-6">
                  <p class="m-b-10 f-w-600">Last Name</p>
                  <h6 class="text-muted f-w-400">{this.props.user.familyname}</h6>
                </div>
                <div class="col-sm-6">
                  <p class="m-b-10 f-w-600">Email</p>
                  <h6 class="text-muted f-w-400">{this.props.user.username}</h6>
                </div>
                <div class="col-sm-6">
                  <p class="m-b-10 f-w-600">Phone</p>
                  <h6 class="text-muted f-w-400">{this.props.user.phone}</h6>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  };

  renderRegister = () => {
    return (
      <div>
        <fieldset>
          <div className="form-group has-feedback required">
            <label htmlFor="login-email" className="col-sm-5">Email</label>
            <div className="col-sm-7">
              <span className="form-control-feedback" aria-hidden="true"></span>
              <input
                type="email"
                name="username"
                id="login-email"
                className="form-control"
                placeholder="Enter Email"
                defaultValue={this.state.username}
                onChange={(e) => this.setState({ username: e.target.value })}
              />
            </div>
          </div>
        </fieldset>
        <div className="form-action">
          <button
            type="submit"
            className="btn btn-light"
            onClick={() => { this.props.Signup({ username: this.state.username }) }}
          >Enter <span className="icon-arrow-right2 outlined"></span></button>

        </div>
      </div>
    );
  };

  renderForgotPassword = () => {
    return (
      <div>
        <fieldset>
          <div className="form-group has-feedback required">
            <label htmlFor="login-email" className="col-sm-5">Email</label>
            <div className="col-sm-7">
              <span className="form-control-feedback" aria-hidden="true"></span>
              <input
                type="email"
                name="username"
                id="login-email"
                className="form-control"
                placeholder="Enter Email"
                defaultValue={this.state.username}
                onChange={(e) => this.setState({ username: e.target.value })}
              />
            </div>
          </div>
        </fieldset>
        <div className="form-action">
          <button
            type="submit"
            className="btn btn-light"
            onClick={() => { this.props.forgotPassword({ username: this.state.username }) }}
          >Enter <span className="icon-arrow-right2 outlined"></span></button>

        </div>
      </div>
    );
  };

  renderEdit = () => {
    return (
      <div class="card user-card-full">
        <div class="row m-l-0 m-r-0">
          <div class="col-sm-4 bg-c-lite-green user-profile">
            <div class="card-block text-center text-white">
              <img src='./images/image_placeholder.png' className="card-img-top rounded-circle" alt="cannot display" />
              <p>{this.props.user.givenname + this.props.user.familyname}</p> <i class=" mdi mdi-square-edit-outline feather icon-edit m-t-10 f-16"></i>
            </div>
          </div>
          <div class="col-sm-8">
            <div class="card-block">
              <h6 class="m-b-20 p-b-5 b-b-default f-w-600">Information</h6>
              <div class="row">
                <div class="col-sm-6">
                  <p class="m-b-10 f-w-600">First Name</p>
                  <h6><input type="text" class="form-control"
                    defaultValue={this.props.user.givenname}
                    onChange={(e) => this.setState({ givenname: e.target.value })}></input></h6>
                </div>
                <div class="col-sm-6">
                  <p class="m-b-10 f-w-600">Last Name</p>
                  <h6><input type="text" class="form-control"
                    defaultValue={this.props.user.familyname}
                    value={this.state.familyname}
                    onChange={(e) => this.setState({ familyname: e.target.value })}></input></h6>
                </div>
                <div class="col-sm-6">
                  <p class="m-b-10 f-w-600">Phone</p>
                  <h6><input type="text" class="form-control"
                    defaultValue={this.props.user.phone}
                    onChange={(e) => this.setState({ phone: e.target.value })}></input></h6>
                </div>
                <div class="col-sm-6">
                  <p class="m-b-10 f-w-600">Address</p>
                  <h6><input type="text" class="form-control"
                    defaultValue={this.props.user.address}
                    onChange={(e) => this.setState({ address: e.target.value })}></input></h6>
                </div>
                <div class="col-sm-6">
                  <p class="m-b-10 f-w-600">Drive account</p>
                  <input type="file" name="file" onChange={this.loadFile} size="sm" id="file" class="inputfile" />
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  };

  renderLogin = () => {
    return (
      <div>
        <fieldset>
          <div className="form-group has-feedback required">
            <label htmlFor="login-email" className="col-sm-5">Email</label>
            <div className="col-sm-7">
              <span className="form-control-feedback" aria-hidden="true"></span>
              <input
                type="text"
                name="username"
                id="login-email"
                className="form-control"
                placeholder="Enter Email"
                value={this.state.username}
                onChange={(e) => this.setState({ username: e.target.value })}
              />
            </div>
          </div>
          <div className="form-group has-feedback required">
            <label htmlFor="login-password" className="col-sm-5">Password</label>
            <div className="col-sm-7">
              <span className="form-control-feedback" aria-hidden="true"></span>
              <div className="login-password-wrapper">
                <input
                  type="password"
                  name="password"
                  id="login-password"
                  className="form-control"
                  placeholder="************"
                  value={this.state.password}
                  onChange={(e) => this.setState({ password: e.target.value })}
                  required
                />
              </div>
            </div>
          </div>
          <button type="submit" className="btn btn-light"
            onClick={() => { this.props.Login({ username: this.state.username, password: this.state.password }) }}> Enter
            <span className="icon-arrow-right2 outlined">
            </span>
          </button>
          <button type="submit" className="btn btn-light"
            onClick={() => this.setMode("forgot")}> Forgot Password
            <span className="icon-arrow-right2 outlined">
            </span>
          </button>
        </fieldset>
      </div>
    );
  };

  render() {
    return (
      <div>
        <Modal
          show={this.props.showModal}
          onHide={this.close}
          onSubmit={this.onSubmit}
          size="lg"
        >
          <Modal.Header closeButton={true}>
            <h2>{this.state.mode === "login" ? "Login"
              : this.state.mode === "edit" ? "Edit Profile"
                : this.state.mode === "profile" ? "Profile"
                  : this.state.mode === "register" ? "Register"
                    : "Change Password"}</h2>
          </Modal.Header>
          <Modal.Body>
            {this.error()}
            {this.state.mode === "login" ? (this.renderLogin())
              : this.state.mode === "edit" ? (this.renderEdit())
                : this.state.mode === "profile" ? (this.renderProfile())
                  : this.state.mode === "register" ? (this.renderRegister())
                    : this.state.mode === "forgot" ? (this.renderForgotPassword())
                      : (this.renderChangePassword())}
          </Modal.Body>
          <Modal.Footer>
            {this.props.user.role === "admin" && this.state.mode === "profile" && <Button className="btn btn-light" onClick={() => this.setMode("register")}> Add Member </Button>}
            {this.state.mode === "profile" && <Button className="btn btn-light" onClick={() => { this.setMode("changePass") }}>Change Password</Button>}
            {this.state.mode === "edit" && <Button className="btn btn-light" onClick={() => { this.setMode("profile") }}>Discard</Button>}
            {this.state.mode === "profile" && <Button className="btn btn-light" onClick={() => { this.setMode("edit") }}>Edit profile</Button>}
            {this.state.mode === "edit" && <Button
              className="btn btn-light"
              onClick={() => {
                this.props.Update({
                  _id: this.props.user._id,
                  givenname: this.state.givenname,
                  familyname: this.state.familyname,
                  phone: this.state.phone,
                  address: this.state.address,
                  client_email: this.state.client_email,
                  private_key: this.state.private_key
                })
                this.setMode("profile")
              }}>Save</Button>}
            {this.state.mode === "profile" && <Button className="btn btn-light" onClick={() => this.props.Logout()}>Logout</Button>}
            <Button className="btn btn-light" onClick={this.close}>Close</Button>
          </Modal.Footer>
        </Modal>
      </div>
    );
  }
}

const mapStateToProps = state => ({
  error: state.errors
});

const mapDispatchToProps = (dispatch) => {
  return {
    Login: (user) => dispatch(postLogin(user)),
    Update: (user) => dispatch(patchUser(user)),
    Signup: (user) => dispatch(postSignup(user)),
    Logout: () => dispatch(getLogout()),
    GLogin: (token) => dispatch(fetchGToken(token)),
    changePassword: (user) => dispatch(changePassword(user)),
    forgotPassword: (user) => dispatch(forgotPassword(user)),
    clearError: () => dispatch(clearError())
  }
};

export default connect(mapStateToProps, mapDispatchToProps)(LoginForm)