function Galler() {
  const ref = useRef();
  const user = useSelector((state) => state.users);
  return (
    <>
      <HeroSection resultref={ref} user={user} />
      <div className="row">
        <div className="col-md-12" ref={ref}>
          <div className="jumbotron jumbotron-fluid py-4">
            <div className="container text-center">
              <h1 className="display-4">Gallery</h1>
            </div>
          </div>
        </div>
        <div className="video">
          <h2>Video</h2>
          <li>
            <div className="Topic-info">
              <h5> 3D scanner: </h5>
            </div>
            {/* <a href="https://www.youtube.com/watch?v=zIVNGKD3eMU&ab_channel=NhatDiep">Experiment video</a> */}
            <iframe
              width="720"
              height="405"
              className="vid"
              src="https://www.youtube.com/embed/4vntC_PdkWY"
              title="YouTube video player"
              frameborder="0"
              allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
              allowfullscreen
            ></iframe>
          </li>
          <li>
            <div className="Topic-info">
              <h5> Welding Robot: </h5>
            </div>
            <iframe
              width="720"
              height="405"
              className="vid"
              src="https://www.youtube.com/embed/zIVNGKD3eMU"
              title="YouTube video player"
              frameborder="0"
              allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
              allowfullscreen
            ></iframe>
            {/* <a href="https://www.youtube.com/watch?v=zIVNGKD3eMU&ab_channel=NhatDiep">Experiment video</a> */}
          </li>
          <li>
            <div className="Topic-info">
              <h5> Non-planar 3D Printing System: </h5>
            </div>
            <iframe
              width="720"
              height="405"
              className="vid"
              src="https://www.youtube.com/embed/zIVNGKD3eMU"
              title="YouTube video player"
              frameborder="0"
              allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
              allowfullscreen
            ></iframe>
            {/* <a href="https://www.youtube.com/watch?v=zIVNGKD3eMU&ab_channel=NhatDiep">Experiment video</a> */}
          </li>
        </div>
        <div className="Image">
          <h2>Photo</h2>
          <Gallery
            images={IMAGES}
            tagStyle={{
              display: "inline",
              padding: ".2em .6em .3em",
              fontSize: "75%",
              fontWeight: "600",
              lineHeight: "1",
              color: "#7deab8",
              background: "rgba(0,0,0,0.65)",
              textAlign: "center",
              whiteSpace: "nowrap",
              verticalAlign: "baseline",
              borderRadius: ".25em",
            }}
            //enableLightbox={true}
            // maxRows={3}
            //backdropClosesModal
            // currentImage={3}
            // isOpen={ true}
          />
        </div>
      </div>
    </>
  );
}

export default Galler;
