export default function researchs(state = [], action) {
  switch (action.type) {
    case 'GOT_RESEARCHS_INFO':
      console.log('reducer member GOT_RESEARCHS_INFO')
      return action.payload
    case 'CREATE_RESEARCH':
      console.log('reducer member PUT_RESEARCH')
      return state
    case 'PUT_RESEARCH':
      console.log('reducer member PUT_RESEARCH')
      return state
    default:
      console.log('reducer member Default')
      return state;
  }
};